import axios from "axios";

import store from "../store";
import { integratedRemoteFetch } from "../assets/data/DataUtil.js";

/**
 * axios
 */
export const config = {
  // `url`  URL
  url: "/user",

  // `method` 
  method: "get", // 

  // `baseURL`  `url`  `url`  URL
  //  `baseURL`  axios  URL
  baseURL: "https://opendata.cwb.gov.tw/api/v1/rest/datastore/",

  // `transformRequest` 
  //  'PUT', 'POST'  'PATCH' 
  //  BufferArrayBufferFormData Stream
  // 
  transformRequest: [
    function (data, headers) {
      //  data 

      return data;
    },
  ],

  // `transformResponse`  then/catch 
  transformResponse: [
    function (data) {
      //  data 

      return data;
    },
  ],

  // `timeout` 
  //  `timeout` 
  timeout: 1000, //  `0` ()

  // `withCredentials` 
  withCredentials: false, // default

  // 
  // headers: { "X-Requested-With": "XMLHttpRequest" },

  // `responseType` 
  // : 'arraybuffer', 'document', 'json', 'text', 'stream'
  // 'blob'
  responseType: "json", // 

  // `onUploadProgress` 
  // 
  onUploadProgress: function (progressEvent) {
    // 
  },

  // `onDownloadProgress` 
  // 
  onDownloadProgress: function (progressEvent) {
    // 
  },
};

/**
 * Axios
 */
const instance = axios.create(config);
// 
instance.interceptors.request.use(
  function (config) {
    // 
    return config;
  },
  function (error) {
    // 
    return Promise.reject(error);
  }
);

// 
instance.interceptors.response.use(
  function (response) {
    // 2xx 
    // 
    integratedRemoteFetch(response, null);
    return response;
  },
  function (error) {
    //  2xx 
    // 
    store.dispatch("apiSotre/commitApiMsg", error.response);
    integratedRemoteFetch(null, error);

    return Promise.reject(error);
  }
);

/**
 * Axios
 * @param {string} cityName 
 * @param {string} townname 
 * @returns {Promise} 
 */
export function getWeather(cityName, townname) {
  return instance
    .get("F-D0047-093", {
      params: {
        Authorization: "CWB-BB5E0510-6698-4BF9-87FF-52ED85F6418D",
        locationId: cityName,
        locationName: townname,
      },
    })
    .then((res) => res)
    .catch((error) => error);
}
