/**
 * RoutefehId
 * @param {string} type Route
 * @returns {string} fehId
 */
export const getFehIdByType = (type) => {
  switch (type) {
    case Route_VUE: {
      return fehId_00001;
    }
    case Route_VUEX: {
      return fehId_00002;
    }
    case Route_VUE_ROUTER: {
      return fehId_00003;
    }
    case Route_GITHUB: {
      return fehId_00004;
    }
    case Route_SOURCE_TREE: {
      return fehId_00005;
    }
    case Route_AXIOS: {
      return fehId_00006;
    }
    case Route_AXIOS: {
      return fehId_00006;
    }
    case Route_MY_SCHEDULE: {
      return fehId_00007;
    }
    case Route_MY_STOCK: {
      return fehId_00008;
    }
  }
};

/**
 * fehId
 * @param {string} id fehid
 * @returns {Promise} 
 */
export const getInsVueContentById = (id) => {
  let getIns = null;
  let msgFmt = null;
  if (insContent.hasOwnProperty(id)) {
    msgFmt = new MsgFormat(
      COR_E0001,
      MSG_TYPE_SUCCESS,
      MSG_POP_ASIDE
    ).getValue();
    getIns = {
      data: { ...insContent[id] },
      msg: { ...msgFmt },
    };
  } else {
    msgFmt = new MsgFormat(
      ERR_E0003,
      MSG_TYPE_SUCCESS,
      MSG_POP_ASIDE
    ).getValue();
    getIns = { data: {} };
  }
  return integratedFetch(getIns, msgFmt);
};
